.calendar_theme_alfa-on-color {
    color: rgba(255, 255, 255, 1);
    background-color: rgb(11, 31, 53);
}

    .calendar_theme_alfa-on-color .calendar__event {
        background-color: rgb(11, 31, 53);
    }

    .calendar_theme_alfa-on-color .calendar__event:after {
            background-color: rgba(255, 255, 255, .4);
        }

    .calendar_theme_alfa-on-color .calendar__day_state_today:before {
            background-color: rgba(255, 255, 255, .4);
        }

    .calendar_theme_alfa-on-color .calendar__arrow_direction_left {
        background-image: url('images/arrow_cl.svg');
    }

    .calendar_theme_alfa-on-color .calendar__arrow_direction_left.calendar__arrow_double {
            background-image: url('images/2_arrow_cl.svg');
        }

    .calendar_theme_alfa-on-color .calendar__arrow_direction_right {
        background-image: url('images/arrow_cr.svg');
    }

    .calendar_theme_alfa-on-color .calendar__arrow_direction_right.calendar__arrow_double {
            background-image: url('images/2_arrow_cr.svg');
        }

    .calendar_theme_alfa-on-color .calendar__day_type_off,
    .calendar_theme_alfa-on-color .calendar__day_type_weekend-off {
        color: rgba(255, 255, 255, .4);
    }

