.icon-button {
    opacity: .9;
    overflow: hidden;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    cursor: pointer;
    outline: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    border: none;
    border-radius: none;
    box-shadow: none;
    transition-duration: 250ms;
    transition-property: background, opacity;
    transition-timing-function: ease;
}

.icon-button, .icon-button *, .icon-button *:before, .icon-button *:after {
        box-sizing: border-box;
    }

.icon-button_hovered,
    .icon-button_focused {
        opacity: 1;
    }

.icon-button_disabled {
        opacity: .25;
        cursor: default;
    }

.icon-button_size_s {
        width: 30px;
        height: 30px;
    }

.icon-button_size_m {
        width: 34px;
        height: 40px;
    }

.icon-button_size_l {
        width: 44px;
        height: 50px;
    }

.icon-button_size_xl {
        width: 52px;
        height: 60px;
    }

