.calendar_theme_alfa-on-white {
    color: rgba(11, 31, 53, .95);
    background-color: rgb(255, 255, 255);
}

    .calendar_theme_alfa-on-white .calendar__event {
        background-color: rgb(255, 255, 255);
    }

    .calendar_theme_alfa-on-white .calendar__event:after {
            background-color: rgba(11, 31, 53, .3);
        }

    .calendar_theme_alfa-on-white .calendar__day_state_today:before {
            background-color: rgba(11, 31, 53, .3);
        }

    .calendar_theme_alfa-on-white .calendar__arrow_direction_left {
        background-image: url('images/arrow_wl.svg');
    }

    .calendar_theme_alfa-on-white .calendar__arrow_direction_left.calendar__arrow_double {
            background-image: url('images/2_arrow_wl.svg');
        }

    .calendar_theme_alfa-on-white .calendar__arrow_direction_right {
        background-image: url('images/arrow_wr.svg');
    }

    .calendar_theme_alfa-on-white .calendar__arrow_direction_right.calendar__arrow_double {
            background-image: url('images/2_arrow_wr.svg');
        }

    .calendar_theme_alfa-on-white .calendar__day_type_off,
    .calendar_theme_alfa-on-white .calendar__day_type_weekend-off {
        color: rgba(11, 31, 53, .3);
    }

