.grid{
    width:100%;
    box-sizing:border-box;

    max-width:1190px;
    margin:0 auto;
    padding:0 12px;
}

@media screen and (min-width: 23.4375em){

.grid{
        padding:0 16px
}
    }

@media screen and (min-width: 25.75em){

.grid{
        padding:0 20px
}
    }

@media screen and (min-width: 48em){

.grid{
        padding:0 32px
}
    }

@media screen and (min-width: 80em){

.grid{
        padding:0 48px
}
    }

.grid__row{
    display:flex;
    flex:0 1 auto;
    flex-direction:row;
    flex-wrap:wrap;

    margin:0 auto;

    box-sizing:border-box;

